import { Button, Select, Flex, Input, Text } from '@chakra-ui/react';

function Convertible(props) {
    const selection = props.data.map(e => { return <option key={e} value={e}>{e}</option> });

    return (
        <Flex justifyContent="center" alignItems="start" flexDirection="column" gap="10px" ml="170px" w="100%">
            <Input w="180px" type="number" placeholder=" " onChange={(e) => { props.setInput(e.target.value) }} />
            <Flex>
                <Select w="180px" ref={props.first}>
                    {selection}
                </Select>
            </Flex>
            <Flex gap="15px">
                <Select w="180px" ref={props.second}>
                    {selection}
                </Select>
            </Flex>
            <Button onClick={() => { props.convert() }}>Convert</Button>

        </Flex>
    )
}

export default Convertible;