import axios from 'axios';

const getData = async (currency) => {
    const currencyValue = await axios.get(`https://www.cbr-xml-daily.ru/daily_json.js`).then((res) => {
        let valute = res.data.Valute[`${currency}`].Value;
        return valute;
    });
    return currencyValue;

};

export default getData;