import { Flex, Text, Input, Button } from '@chakra-ui/react';
import { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { updateHistory } from '../historySlice';

function InputCalc(props) {
    const [result, setResult] = useState('');
    const [counts, setCounts] = useState('');
    const dispatch = useDispatch();
    useEffect(() => { document.querySelector("input").focus() });


    function updateCounts(e) {
        const expressions = /[0-9]|\)/;
        const lastNumber = e.target.value[e.target.value.length - 2];
        if (!expressions.test(lastNumber) && !expressions.test(e.nativeEvent.data) && e.nativeEvent.data != null) return;
        if (expressions.test(e.nativeEvent.data)) setResult(eval(e.target.value));
        setCounts(e.target.value);
    }

    function sendDataToHistory(e) {
        if (e.nativeEvent.key === "Enter") {
            props.onKeyDown(counts);
            setCounts('');
        }
    }

    return (
        <Flex mt="10px" flexDirection="column" justifyContent="center" alignItems="center">
            <Button onClick={() => dispatch(updateHistory(result))} w="fit-content" mb="10px">Add to history</Button>
            <Flex justifyContent="center" alignItems="center" border="2px" borderRadius="8px" borderColor="gray.50">
                <Input border="transparent" type="text" onInput={(e) => { updateCounts(e) }} onKeyDown={(e) => { sendDataToHistory(e) }} value={counts} />
                <Text textColor="tomato" px="4px"> Result: {result} </Text>
            </Flex>
        </Flex>
    )
}

export default InputCalc;