import { Flex, Text, Button } from '@chakra-ui/react';
import { useState } from 'react';
import Drugging from './Drugging';
import { useDispatch } from 'react-redux';
import { updateHistory } from '../historySlice';

function ClickCalc(props) {
    const [counts, setCounts] = useState('0');
    const [result, setResult] = useState('');
    const dispatch = useDispatch();

    function applyExpression(countedNumber) {
        setCounts(countedNumber);
        const expressionFunction = new Function('return ' + counts);;
        setResult(expressionFunction());
    }

    function Numbers(props) {
        const nums = ['7', '8', '9', '4', '5', '6', '1', '2', '3', '0', ','].map(
            number => {
                return <Button
                    onClick={(e) => {
                        if (props.data !== '0') {
                            props.onClick(props.data + e.target.innerHTML)
                        } else {
                            props.onClick(e.target.innerHTML)
                        }
                    }}
                    key={number} w="60px" h="60px" margin="4px">
                    {number}
                </Button>
            }
        )
        return (
            <Flex flexWrap="wrap" w="100%"> {nums} </Flex>
        )
    }

    function CountButton(props) {
        const expressions = /\+|-|\/|\*| /;
        const lastNumber = props.data[props.data.length - 1];
        function checkExpressionType() {
            if (expressions.test(lastNumber)) return;
            props.onClick(props.data + props.expression);
        }
        return (
            <Button className="droppable" onClick={() => {
                checkExpressionType()
            }} margin="4px" bg="blue" w="60px" h="60px">
                {props.expression}
            </Button>
        )
    }

    return (
        <Drugging
            result={result}
            setResult={setResult}
            setHistory={props.onClick}
        >
            <Flex justifyContent="center" alignItems="center" h="100vh">
                <Flex gap="5px" flexDirection="column" justifyContent="center" alignItems="center" w="360px">
                    <Flex w="98%" justifyContent="spaceBetween">
                        <Text display="flex" justifyContent="start" alignItems="center" bg="gray.50" w="100%" p="10px">
                            {counts}
                        </Text>
                        <Text display="flex" w="fit-content" alignItems="center" textColor="tomato" p="10px">
                            {result}
                        </Text>
                    </Flex>
                    <Button onClick={() => dispatch(updateHistory(result))}>Add to history</Button>
                    <Flex>
                        <Numbers data={counts} onClick={setCounts} />
                        <Flex flexDirection="column">
                            <CountButton data={counts} expression={'+'} onClick={applyExpression} />
                            <CountButton data={counts} expression={'-'} onClick={applyExpression} />
                            <CountButton data={counts} expression={'*'} onClick={applyExpression} />
                            <CountButton data={counts} expression={'/'} onClick={applyExpression} />
                        </Flex>
                        <Button bg="tomato" m="4px" w="60px" h="60px" flexShrink="0" onClick={() => {
                            setResult(eval(counts));
                            setCounts('0');
                            props.onClick(counts);
                        }}>
                            =
                        </Button>
                    </Flex>
                </Flex >
            </Flex>
        </Drugging>

    )
}

export default ClickCalc;