import './App.css';
import { useState } from 'react';
import Calculator from './components/Calculator';
import Converter from './components/Converter';
import { Routes, Route, Link } from 'react-router-dom';
import { Box, Button, Menu, MenuList, MenuItem, MenuButton, IconButton, Flex } from '@chakra-ui/react';
import { HamburgerIcon } from '@chakra-ui/icons';
import { useSelector } from 'react-redux';
import { historyState } from '../src/historySlice';

function MainMenu(props) {
    return (
        <Box>
            <Menu >
                <MenuButton as={IconButton} icon={<HamburgerIcon />} m="10px"></MenuButton>
                <MenuList z-index="10">
                    <MenuItem>
                        <Link to="/calculator">Calculator</Link>
                    </MenuItem>
                    <MenuItem>
                        <Link to="/converter">Converter</Link>
                    </MenuItem>
                </MenuList>
            </Menu>
        </Box>
    )
}

function App() {
    const history = useSelector(historyState).map(e => { return <Button>{e}</Button> });
    const [mode, setMode] = useState('Calculator');
    let application;

    switch (mode) {
        case 'Calculator':
            application = <Calculator />;
            break;
        case 'Converter':
            application = <Converter />;
            break;
        default:
            application = <Calculator />;
    }

    return (
        <Box w="360px" h="90vh">
            <Flex gap="10px" color="tomato" justifyContent="start" alignItems="center" m="10px" w="fit-content">History: {history}</Flex>
            <MainMenu onClick={setMode} />
            <Routes>
                <Route path="/" element={<Calculator />} />
                <Route path="/converter" element={<Converter />} />
                <Route path="/calculator" element={<Calculator />} />
            </Routes>
        </Box>
    );
}

export default App;
