import { useState, useRef } from 'react';
import { Button, Flex, Text, Box, List, SlideFade, useDisclosure } from '@chakra-ui/react';
import { SettingsIcon } from '@chakra-ui/icons';
import getData from '../parser';
import Convertible from './Convertible';
import { useSelector, useDispatch } from 'react-redux';
import { updateHistory, historyState } from '../historySlice';

function MenuConv(props) {
    const { isOpen, onToggle } = useDisclosure();
    return (
        <Box>
            <SettingsIcon onClick={onToggle} w="45px" h="45px" p="5px" m="5px" />
            <SlideFade in={isOpen} offsetY="-20px" unmountOnExit>
                <Flex bg="gray.100" p="10px" m="4px" borderRadius="8px" h="150px" w="160px" position="absolute" right="50px">
                    <List z-index="10">
                        <Button onClick={() => { props.onClick('Money'); onToggle() }}>Money</Button>
                        <Button onClick={() => { props.onClick('Distanse'); onToggle() }}>Distanse</Button>
                        <Button>Settings</Button>
                    </List>
                </Flex>
            </SlideFade>
        </Box>
    )
}

function Converter() {
    const [input, setInput] = useState(0);
    const [result, setResult] = useState(0);
    const [mode, setMode] = useState('Distance');
    const first = useRef();
    const second = useRef();
    const data = [" ", ""];
    const dataDistanse = ["Meters", "Centimeters"];
    const history = useSelector(historyState);
    const dispatch = useDispatch();

    let converter;
    switch (mode) {
        case 'Distanse':
            converter = <Convertible setInput={setInput} convert={convert} first={first} second={second} data={dataDistanse} />;
            break;
        case 'Money':
            converter = <Convertible setInput={setInput} convert={convertMoney} first={first} second={second} data={data} />;
            break;
        default:
            converter = <Convertible setInput={setInput} convert={convert} first={first} second={second} data={dataDistanse} />;
    }

    function convert() {
        if (first.current.value === "Meters") {
            switch (second.current.value) {
                case "Centimeters":
                    setResult(input * 100);
                    break;
                case "Meters":
                    setResult(input);
                    break;
                default: setResult(input);
            }
        }
        if (first.current.value === "Centimeters") {
            switch (second.current.value) {
                case "Centimeters":
                    setResult(input);
                    break;
                case "Meters":
                    setResult(input / 100);
                    break;
                default: setResult(input);
            }
        }
    }
    async function convertMoney() {
        if (first.current.value === " ") {
            const currency = 'USD';
            getData(currency).then(x => {
                switch (second.current.value) {
                    case "":
                        setResult(input * Number(x));
                        break;
                    case " ":
                        setResult(input);
                        break;
                }
            })
        } else {
            const currency = 'USD';
            getData(currency).then(x => {
                switch (second.current.value) {
                    case "":
                        setResult(input);
                        break;
                    case " ":
                        setResult(input / Number(x));
                        break;
                }
            })
        };
    }

    return (
        <Flex justifyContent="center" alignItems="start" flexDirection="column" gap="10px" w="100%">
            <Flex justifyContent="center" alignItems="center" flexDirection="column" gap="10px" w="100%">
                <MenuConv onClick={setMode} />
                <Button onClick={() => dispatch(updateHistory(result))}>Add to history</Button>
                {converter}
                <Text w="100%" ml="170px">Result: {result}</Text>
            </Flex>
        </Flex>
    )
}

export default Converter;